import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Summary } from 'src/app/entities/summary';
import { SummaryResumeService } from 'src/app/services/summary-resume/summary-resume.service';
import { Router } from '@angular/router';
import { Contratante, Contrato, Configuracion, Bien } from 'src/app/entities/business-control';
import { BusinessControlService } from 'src/app/services/business-control/business-control.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-business-control-settings',
  templateUrl: './business-control-settings.component.html',
  styleUrls: ['./business-control-settings.component.css']
})
export class BusinessControlSettingsComponent implements OnInit {
  configuracionForm: FormGroup;
  config: Configuracion | null = null;
  bienform: FormGroup;
  contratoForm: FormGroup;
  isConfigured = false;
  bienes: Bien[] = [];
  selectedBien: Bien | null = null;
  registrarNuevaBien = false;
  registrarNuevoContrato = false;
  tipoBien: 'Arrendamiento' | 'Credito' = 'Arrendamiento';
  @ViewChild('fileInput') fileInput: ElementRef<HTMLInputElement>;

  constructor(private toastr: ToastrService, private router: Router, private fb: FormBuilder, private comprobantesService: BusinessControlService) {
    this.configuracionForm = this.fb.group({
      contratanteCedula: ['', Validators.required],
      contratanteNombre: ['', Validators.required],
      contratanteTelefono: ['', Validators.required],
      contratanteDireccion: ['', Validators.required],
      contratanteEmail: ['', Validators.required],
      contratanteMostrarDatosPersonales: [false],
      submitted: [false]
    });
    this.bienform = this.fb.group({
      direccion: ['', Validators.required],
      barrio: ['', Validators.required],
      ciudad: ['', Validators.required],
      submitted: [false]
    });
    this.contratoForm = this.fb.group({
      contratoNombre: ['', Validators.required],
      contratoCedula: ['', Validators.required],
      contratoTelefono: ['', Validators.required],
      contratoMensualidad: ['', Validators.required],
      contratoValorDeposito: ['', Validators.required],
      contratoFechaInicio: ['', Validators.required],
      contratoObservacion: [''],
      submitted: [false]
    });
  }

  ngOnInit(): void {
    this.isConfigured = false;
    this.selectedBien = null;
    this.config = this.comprobantesService.getConfiguracion();
    if (this.config) {
      this.isConfigured = true;
      this.bienes = this.config.bienes;
      this.selectedBien = this.bienes[0];
      if (!this.selectedBien || !this.selectedBien.contratoActivo) {
        this.registrarNuevoContrato = true;
        return;
      }
      this.tipoBien = 'Arrendamiento';
    }
    this.configuracionForm.reset();
    this.bienform.reset();
    this.contratoForm.reset();
  }

  onSubmitInitial(): void {
    this.configuracionForm.patchValue({ submitted: true });
    if (this.configuracionForm.valid) {
      const formValues = this.configuracionForm.value;

      const newContratante: Contratante = {
        nombre: formValues.contratanteNombre,
        identificacion: formValues.contratanteCedula,
        telefono: formValues.contratanteTelefono,
        direccion: formValues.contratanteDireccion,
        email: formValues.contratanteEmail,
        mostrarDatosPersonales: formValues.contratanteMostrarDatosPersonales,
        firmaDigital: ''
      };

      if (!this.isConfigured) {
        this.isConfigured = true;
        this.registrarNuevaBien = true;
        this.comprobantesService.setContratante(newContratante);
        this.ngOnInit();
      } else {
        this.toastr.info("Ya configuraste el sistema!", "Business Control");
      }
    }
  }

  onSubmitBien(): void {
    this.bienform.patchValue({ submitted: true });
    if (this.bienform.valid) {
      const formValues = this.bienform.value;
      const newBien: Bien = {
        id: '',
        direccion: formValues.direccion,
        barrio: formValues.barrio,
        ciudad: formValues.ciudad,
        tipo: this.tipoBien,
        contratos: [],
        comprobantes: [],
        serviciosPublicos: []
      };
      this.registrarNuevaBien = false;
      this.registrarNuevoContrato = true;
      this.comprobantesService.addBien(newBien);
      this.ngOnInit();
    }
  }

  onSubmitContrato(): void {
    this.contratoForm.patchValue({ submitted: true });
    if (this.contratoForm.valid) {
      const formValues = this.contratoForm.value;

      const newContrato: Contrato = {
        tipoDocumento: 'CC',
        id: formValues.contratoCedula,
        nombre: formValues.contratoNombre,
        identificacion: formValues.contratoCedula,
        telefono: formValues.contratoTelefono,
        observacion: formValues.contratoObservacion,
        mensualidad: formValues.contratoMensualidad,
        valorDeposito: formValues.contratoValorDeposito,
        inicio: formValues.contratoFechaInicio,
        tipo: this.selectedBien.tipo,
        activo: true,
      };
      this.config.conceptos = [
        {
          descripcion: "Mensualidad",
          valor: formValues.contratoMensualidad
        },
        {
          descripcion: "Depsito",
          valor: formValues.contratoValorDeposito
        },
        {
          descripcion: "Depsito (50%)",
          valor: formValues.contratoValorDeposito / 2
        },
        {
          descripcion: "Otro",
          valor: 0
        },
      ];
      this.comprobantesService.saveConfiguracion(this.config);
      this.comprobantesService.addContrato(this.selectedBien.id, newContrato);
      this.ngOnInit();
    }
  }

  onBienSelect(bienId: string): void {
    this.selectedBien = this.comprobantesService.getBienById(bienId);
  }

  downloadConfig(): void {
    this.comprobantesService.downloadConfig();
  }

  isProcessingFile = false;

  // Mtodo para manejar el cambio de archivo
  onFileChange(event: Event) {
    if (this.isProcessingFile) {
      return;  // Si ya se est procesando un archivo, salir
    }
    this.isProcessingFile = true;

    const input = event.target as HTMLInputElement;
    if (input.files && input.files.length) {
      const file = input.files[0];
      this.readAndValidateFile(file);
      input.value = ''; // Restablecer el valor del input
    } else {
      this.isProcessingFile = false;
    }
  }


  // Mtodo para leer el archivo y validarlo
  readAndValidateFile(file: File) {
    if (file.size === 0) {
      this.toastr.info("Archivo vacio", "Business Control");
      this.isProcessingFile = false;
      return;
    }

    const reader = new FileReader();
    reader.onload = (e: ProgressEvent<FileReader>) => {
      try {
        this.isProcessingFile = false;
        const json = JSON.parse(reader.result as string);
        if (json) {
          this.config = this.comprobantesService.saveConfiguracion(json);
          this.toastr.success("Datos importados correctamente", "Business Control");
          this.ngOnInit();
        }
      } catch (error) {
        this.toastr.error('Error al procesar el archivo JSON - ' + JSON.stringify(error) + ' | ' + JSON.stringify(reader.result), "Business Control");
        alert();
      } finally {
        reader.onload = null;
      }
    };
    reader.readAsText(file);
  }

  deleteData(): void {
    const result = confirm('Ests seguro de que deseas continuar?');
    if (result) {
      this.comprobantesService.deleteData();
      this.ngOnInit();
    } else {
      this.toastr.info("Operacin de eliminacin cancelada", "Business Control");
    }
  }

}
