import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Contrato, Comprobante, Concepto, Configuracion, ReciboServicio, Bien } from 'src/app/entities/business-control';
import { Summary } from 'src/app/entities/summary';
import { BusinessControlService } from 'src/app/services/business-control/business-control.service';
import { SummaryResumeService } from 'src/app/services/summary-resume/summary-resume.service';
import { Chart } from 'chart.js';
import { chartOptions, parseOptions } from 'src/app/variables/charts';
import { PdfService } from 'src/app/services/pdf/pdf.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-business-control',
  templateUrl: './business-control.component.html',
  styleUrls: ['./business-control.component.css']
})
export class BusinessControlComponent implements OnInit {
  comprobanteForm: FormGroup;
  selectedBien: Bien | null = null;
  config: Configuracion | null = null;
  servicioForm: FormGroup;
  newComprobante: Comprobante | null = null;
  chart: any;
  observacionComprobante: string = '';
  metodoPagoComprobante: string = '';
  @ViewChild('pdfContent', { static: false }) pdfContent: ElementRef;
  comprobanteToPdf: Comprobante | null = null;
  firmaDigital: string = '';
  isWebShareSupported: boolean = false;

  constructor(private toastr: ToastrService, private fb: FormBuilder, private comprobantesService: BusinessControlService, private router: Router, private pdfService: PdfService) {
    this.comprobanteForm = this.fb.group({
      descripcion: ['', Validators.required],
      descripcionTemp: [''],
      valor: ['', Validators.required],
      submitted: [false]
    });
    this.servicioForm = this.fb.group({
      tipo: ['', Validators.required],
      valor: ['', Validators.required],
      fechaGeneracion: ['', Validators.required],
      fechaPago: ['', Validators.required],
      submitted: [false]
    });
    this.isWebShareSupported = !!navigator.share;
  }

  private InicializarComprobante() {
    this.newComprobante = {
      id: new Date().getTime().toString(),
      conceptos: [],
      total: 0,
      fechaGeneracion: new Date(),
      periodo: {
        inicio: new Date(),
        fin: new Date(),
      },
      observacion: '',
      contratoId: '',
      medioPago: 'Efectivo',
    };
  }

  ngOnInit(): void {
    this.config = this.comprobantesService.getConfiguracion();
    if (!this.config || this.config.bienes.length == 0) {
      this.router.navigate(['/business-control-settings']);
      return;
    }
    if (this.config.bienes.length > 0) {
      if (this.selectedBien) {
        this.onBienSelect(this.selectedBien.id);
      } else {
        this.onBienSelect(this.config.bienes[0].id);
      }
    }
    this.firmaDigital = this.config.contratante.firmaDigital;
    this.onConceptoSelected(this.config.conceptos[0].descripcion);
    this.InicializarComprobante();
    this.observacionComprobante = "";
    this.metodoPagoComprobante = this.config.mediosPago[0];
  }

  onBienSelect(bienId: string): void {
    this.selectedBien = this.config.bienes.find(v => v.id === bienId) || null;
    this.comprobanteToPdf = null;
    setTimeout(() => {
      this.comprobanteToPdf = this.selectedBien.comprobantes[0];
      this.renderizarGraficos();
    }, 100);
  }

  onConceptoSelected(descripcion: string): void {
    const concepto = this.config.conceptos.find(v => v.descripcion === descripcion);
    if (concepto) {
      this.comprobanteForm.patchValue({ valor: concepto.valor });
      this.comprobanteForm.patchValue({ descripcionTemp: concepto.descripcion });
      this.comprobanteForm.patchValue({ descripcion: concepto.descripcion });
    }
  }


  addConcepto(): void {
    this.comprobanteForm.patchValue({ submitted: true });
    if (this.comprobanteForm.valid) {
      const formValues = this.comprobanteForm.value;
      const newConcepto: Concepto = {
        descripcion: formValues.descripcion,
        valor: formValues.valor
      };
      this.newComprobante.conceptos.push(newConcepto);
      this.newComprobante.total += newConcepto.valor;
      this.comprobanteForm.reset();
    }
  }

  removeConcepto(index: number): void {
    this.newComprobante.total -= this.newComprobante.conceptos[index].valor;
    this.newComprobante.conceptos.splice(index, 1);
  }

  generateComprobante(): void {
    if (this.selectedBien) {
      this.newComprobante.observacion = this.observacionComprobante;
      this.newComprobante.medioPago = this.metodoPagoComprobante;
      this.comprobantesService.addComprobante(this.selectedBien.id, this.newComprobante);
      this.ngOnInit();
      this.toastr.success('Comprobante generado correctamente', 'Business Control!');
    }
  }

  registerServicio(): void {
    this.servicioForm.patchValue({ submitted: true });
    if (this.selectedBien && this.servicioForm.valid) {
      const newServicio: ReciboServicio = {
        tipo: this.servicioForm.value.tipo,
        valor: this.servicioForm.value.valor,
        fechaGeneracion: this.servicioForm.value.fechaGeneracion,
        fechaPago: this.servicioForm.value.fechaPago
      };
      this.comprobantesService.addServicioPublico(this.selectedBien.id, newServicio);
      this.servicioForm.reset();
      this.ngOnInit();
      this.toastr.success('Servicio registrado correctamente', 'Business Control!');
    }
  }

  renderizarGraficos(): void {
    this.generarGraficaIngresosVsGastos();
  }

  generarGraficaIngresosVsGastos(): void {
    const selectedBien = this.selectedBien;

    // Crear un mapa para agrupar los datos por mes
    const labelsMap = new Map<string, { ingresos: number, aguagastos: number, luzgastos: number, gasgastos: number, internetgastos: number, otrosgastos: number }>();

    // Procesar comprobantes para agrupar ingresos por mes
    selectedBien.comprobantes.forEach(comprobante => {
      const monthYear = new Date(comprobante.fechaGeneracion).toLocaleString('default', { month: '2-digit', year: 'numeric' });
      if (!labelsMap.has(monthYear)) {
        labelsMap.set(monthYear, { ingresos: 0, aguagastos: 0, luzgastos: 0, gasgastos: 0, internetgastos: 0, otrosgastos: 0 });
      }
      const data = labelsMap.get(monthYear);
      data.ingresos += comprobante.total;
    });

    // Procesar servicios pblicos para agrupar gastos por tipo y mes
    selectedBien.serviciosPublicos.forEach(servicio => {
      const monthYear = new Date(servicio.fechaGeneracion).toLocaleString('default', { month: '2-digit', year: 'numeric' });
      if (!labelsMap.has(monthYear)) {
        labelsMap.set(monthYear, { ingresos: 0, aguagastos: 0, luzgastos: 0, gasgastos: 0, internetgastos: 0, otrosgastos: 0 });
      }
      const data = labelsMap.get(monthYear);
      switch (servicio.tipo) {
        case 'Agua':
          data.aguagastos += servicio.valor;
          break;
        case 'Luz':
          data.luzgastos += servicio.valor;
          break;
        case 'Gas':
          data.gasgastos += servicio.valor;
          break;
        case 'Internet':
          data.internetgastos += servicio.valor;
          break;
        case 'Otro':
          data.otrosgastos += servicio.valor;
          break;
      }
    });


    this.updateChart(labelsMap);

  }

  updateChart(labelsMap: Map<string, { ingresos: number, aguagastos: number, luzgastos: number, gasgastos: number, internetgastos: number, otrosgastos: number }>): void {
    // Convertir el mapa a arrays para labels y datasets
    const labels = Array.from(labelsMap.keys());
    const ingresos = labels.map(label => labelsMap.get(label).ingresos);
    const aguagastos = labels.map(label => labelsMap.get(label).aguagastos);
    const luzgastos = labels.map(label => labelsMap.get(label).luzgastos);
    const gasgastos = labels.map(label => labelsMap.get(label).gasgastos);
    const internetgastos = labels.map(label => labelsMap.get(label).internetgastos);
    const otrosgastos = labels.map(label => labelsMap.get(label).otrosgastos);

    const data = {
      labels: labels,
      datasets: [
        { label: 'Arriendo', data: ingresos, backgroundColor: '#4caf50', fill: false }
      ]
    }

    if (this.selectedBien.tipo == 'Arrendamiento') {
      data.datasets.push({ label: 'Agua', data: aguagastos, backgroundColor: '#2196f3', fill: false },
        { label: 'Luz', data: luzgastos, backgroundColor: '#ffeb3b', fill: false },
        { label: 'Gas', data: gasgastos, backgroundColor: '#f44336', fill: false },
        { label: 'Internet', data: internetgastos, backgroundColor: '#9c27b0', fill: false },
        { label: 'Otros', data: otrosgastos, backgroundColor: '#ff9800', fill: false })
    }

    if (!this.chart) {
      // Generar el grfico
      this.chart = new Chart('canvas', {
        type: 'line',
        data: data,
        options: {
          responsive: true,
          scales: {
            // xAxes: [{
            //   display: false // Oculta los labels en el eje X
            // }],
            yAxes: [{
              display: false, // Oculta los labels en el eje Y
              ticks: {
                beginAtZero: true
              }
            }]
          },
          legend: {
            // display: false // Opcional: Oculta la leyenda si solo quieres ver las lneas
          }
        }
      });
    }
    this.chart.data = data;

    // Actualiza el grfico
    this.chart.update();
  }

  generatePDF(comprobante: ComprobanteComponent) {
    this.comprobanteToPdf = null;
    comprobante.generandoPdf = true;
    setTimeout(() => {
      this._generatePdf(comprobante);
    }, 100);
  }

  sharedPDF(comprobante: ComprobanteComponent) {
    this.comprobanteToPdf = null;
    comprobante.compartiendoPdf = true;
    setTimeout(() => {
      this._shareByAppsFromSO(comprobante);
    }, 100);
  }

  showComprobante(comprobante: ComprobanteComponent) {
    const url = this.comprobantesService.generateUrlFirmada(comprobante);
    window.open(url, '_blank');
  }

  private _generatePdf(comprobante: ComprobanteComponent) {
    this.comprobanteToPdf = comprobante;
    setTimeout(async () => {
      if (this.pdfContent) {
        await this.pdfService.generatePDF(`Comprobante Nro ${comprobante.id}`, this.pdfContent.nativeElement);
        comprobante.generandoPdf = false;
        this.toastr.info('Pdf generado con exto', 'Business Control!');
      } else {
        this.toastr.error('El contenido del PDF no est disponible.', 'Business Control!');
      }
    }, 0);
  }

  private _shareByAppsFromSO(comprobante: ComprobanteComponent) {
    try {
      this.comprobanteToPdf = comprobante;
      setTimeout(() => {
        if (this.pdfContent) {
          // this.pdfService.generatePDF(comprobante.id, this.pdfContent.nativeElement);
          this.pdfService.getPdfInBlob(comprobante.id, this.pdfContent.nativeElement).then((pdfBlob: Blob) => {
            this._sharedPdf(pdfBlob, comprobante);
            comprobante.compartiendoPdf = false;
          });
        } else {
          this.toastr.error('El contenido del PDF no est disponible.', 'Business Control!');
        }
      }, 0);
    } catch (error) {
      this.toastr.error('El contenido del PDF no est disponible : ' + JSON.stringify(error), 'Business Control!');
    }

  }

  private _sharedPdf(pdfBlob: Blob, comprobante: Comprobante) {
    const file = new File([pdfBlob], `Comprobante Nro ${comprobante.id}.pdf`, { type: 'application/pdf' });
    if (navigator.share) {
      navigator.share({
        title: `Comprobante Nro ${comprobante.id}.pdf`,
        text: `Hola ${comprobante.bien.contratoActivo.nombre},\n\nEspero que te encuentres bien.\n\nTe comparto con gusto el comprobante de pago correspondiente al periodo del ${this.comprobantesService.formatoInternacional(comprobante.periodo.inicio)} al ${this.comprobantesService.formatoInternacional(comprobante.periodo.fin)}. Este comprobante verifica el cumplimiento de nuestras obligaciones y proporciona detalles completos sobre la transaccin realizada.\n\nSi tienes alguna pregunta o necesitas informacin adicional, no dudes en contactarme.\n\nSaludos cordiales,\n${comprobante.contratante.nombre}`,
        files: [file]
      }).then(() => {
        this.toastr.info('Pdf generado con exto, compartelo!', 'Business Control!');
      }).catch((error) => {
        this.toastr.error('Error al compartir: ' + JSON.stringify(error), 'Business Control!');
      });
    } else {
      this.toastr.error('La funcin de compartir no est disponible en este navegador.', 'Business Control!');
    }
  }

}

interface ComprobanteComponent extends Comprobante {
  generandoPdf: boolean;
  compartiendoPdf: boolean;
}